.about_me {
  margin-top: 200px;
}


.title {
  color: #E1DAD2;
  font-family: 'Cousine', monospace;
  font-size: 121px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  margin:0px;

}

.info {
  display: flex;
  justify-content: center;
}

.photo {
  width: 525px;
  height: 768px;
  border-radius: 20px;
  -webkit-transition: background 1s, border-radius 1s;
  transition: background 1s, border-radius 1s;
 
}

.photo:hover {
  border-radius: 0px;

}

.body_text {
  width: 515px;
  height: 687px;
  flex-shrink: 0;
  color: #E1DAD2;
  font-family: Outfit;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: 35px;
  /* 140% */
  letter-spacing: 2.5px;
  text-transform: uppercase;
  padding-left: 25px;
  margin:0px;
}

.icons{
  display: flex;
  justify-content: space-evenly;
}

.icon{
  width: 75px;
  height:75px;
  
}